import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreDocument, QueryFn } from '@angular/fire/firestore';

@Injectable({
  providedIn: 'root'
})
export class FiredataService {

  constructor(private fs: AngularFirestore) { }

  getLatest(docref: any) {
    return this.fs.doc(docref).collection('data', ref => ref.orderBy('Submitted', 'desc').limit(1)).snapshotChanges();
  }

  getData(key: AngularFirestoreDocument<firebase.firestore.DocumentData>, ref: QueryFn) {
    return key.collection('data', ref).valueChanges();
  }

  getDataState(key: AngularFirestoreDocument<firebase.firestore.DocumentData>, ref: QueryFn) {
    return key.collection('data', ref).snapshotChanges();
  }

  toDoc(uid: string, key: string) {
    return this.fs.collection('uid').doc(uid).collection('keys').doc(key);
  }
}
