import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FiredataService } from 'src/app/core/firedata.service';
import { AngularFirestoreDocument, QueryFn } from '@angular/fire/firestore';
import { map, take } from 'rxjs/operators';
import { Key, Data } from 'src/app/core/data';
import { Observable } from 'rxjs';
import { KeyService } from 'src/app/core/key.service';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {
  private keyRef: AngularFirestoreDocument<firebase.firestore.DocumentData>;
  course: Observable<CourseData>;
  bardata: Observable<BarInfo[]>;
  displayQns: string[];
  displayStudents: string[];
  $dateData: Observable<DateInfo[]>;
  selectedDate: DateInfo;
  timeData: Date[];
  timeEnd: Date;
  barMax: number[];
  loading: boolean;
  hasData: boolean;
  hasCourse: boolean;
  setDateBoo = false;
  hideSlider = true;
  max: number;
  query: firebase.firestore.CollectionReference | firebase.firestore.Query;
  unique = (value: any, index: any, self: { indexOf: (arg0: any) => void; }) => {
    return self.indexOf(value) === index;
  }

  constructor(private fd: FiredataService, private router: Router, private route: ActivatedRoute, private ks: KeyService) {
    this.loading = true;
    this.hasData = false;
    this.displayQns = [];
    this.displayStudents = [];
    this.max = 0;
    this.timeData = [];
  }

  ngOnInit() {
    this.keyRef = this.fd.toDoc(this.route.snapshot.paramMap.get('id'), this.route.snapshot.paramMap.get('key'));
    this.getCourse();
    this.fd.getData(this.keyRef, ref => ref.orderBy('Submitted', 'desc').limit(1))
      .pipe(take(1))
      .subscribe((d: Data[]) => {
        if (d.length === 0) {
          this.hasData = false;
        } else {
          this.hasData = true;
          this.setupDate(this.dateToDateInfo((d[0].Submitted as firebase.firestore.Timestamp).toDate()));
          this.$dateData = this.fd.getData(this.keyRef, ref => ref.orderBy('Submitted'))
            .pipe(map(action => {
              if (action.length === 0) {
                this.hasData = false;
                return [];
              }
              this.hasData = true;
              return action;
            }),
              map(data => {
                if (data.length === 0) {
                  return [];
                }
                const dateArray = data
                  .map(item => this.transformData(item))
                  .map(({ Submitted }) => (Submitted as Date).toDateString())
                  .filter(this.unique)
                  .map(text => this.dateToDateInfo(new Date(text)));
                return dateArray;
              })
            );
        }
      });
  }

  dateToDateInfo(date: Date): DateInfo {
    const text = date.toDateString();
    const textArray = text.split(' ');
    return {
      dayText: textArray[0],
      dateText: textArray[2],
      monthText: ['January', 'February', 'March', 'April', 'May', 'June',
        'July', 'August', 'September', 'October', 'November', 'December'][Number(new Date(text).getMonth())],
      yearText: textArray[3],
      text,
      date: new Date(text)
    };
  }


  setupDate(date: DateInfo) {
    if (!this.setDateBoo) {
      this.setDateBoo = true;
      this.selectedDate = date;
      this.changeDate();
    }
  }

  getCourse() {
    this.keyRef.valueChanges()
      .pipe(take(1))
      .subscribe(i => {
        if (!i) {
          alert('Course has been deleted/not found');
          this.router.navigate(['/dashboard']);
        } else {
          this.course = this.keyRef.snapshotChanges().pipe(map(item => {
            if (!item.payload.exists) {
              alert('Course has been deleted/not found');
              this.router.navigate(['/dashboard']);
              return;
            }
            this.hasCourse = true;
            return {
              ref: this.keyRef,
              data: item.payload.data() as Key,
              key: item.payload.id as string,
            };
          }));
        }
      });
  }

  refreshDate() {
    const { date } = this.selectedDate;
    const dateHolder = new Date(date);
    dateHolder.setDate(dateHolder.getDate() + 1);
    const endDate = dateHolder;
    this.getBarData(ref => {
      this.query = ref.where('Submitted', '>', date).where('Submitted', '<', endDate).orderBy('Submitted');
      return this.query;
    });
  }

  changeDate(d?: DateInfo) {
    if (this.selectedDate.text !== (d ? d.text : null)) {
      this.hideSlider = true;
      this.displayQns = [];
      this.displayStudents = [];
      const { date } = this.selectedDate = d || this.selectedDate;
      const dateHolder = new Date(date);
      dateHolder.setDate(dateHolder.getDate() + 1);
      const endDate = dateHolder;
      this.getBarData(ref => {
        this.query = ref.where('Submitted', '>', date).where('Submitted', '<', endDate).orderBy('Submitted');
        return this.query;
      });
    }
  }

  getBarData(r?: QueryFn) {
    this.bardata = this.fd.getDataState(this.keyRef, (r ? r : ref => ref)).pipe(map(action => {
      let allData = action.map(item => {
        return {
          ref: this.ks.referenceToAngularReference(item.payload.doc.ref),
          ...this.transformData(item.payload.doc.data())
        };
      });
      this.timeData = allData.map(({ Submitted }) => Submitted as Date);
      if (this.timeData.length > 1) {
        this.hideSlider = false;
        if (this.timeEnd) {
          allData = allData.filter(({ Submitted }) => Submitted <= this.timeEnd);
        }
      } else {
        this.hideSlider = true;
      }
      return this.transformForBarChart(allData);
    }));
  }

  transformForBarChart(dataArray: Answer[]) {
    const uniqueQuestions = dataArray.map(({ QnLabel }) => QnLabel).filter(this.unique).map(QnLabel => {
      const Answers = dataArray.filter(data => data.QnLabel === QnLabel);
      const type = !Answers.find(s => s.Code || s.Answer)
        ? 'MS' // Milestone
        : !Answers.find(s => s.Code)
          ? 'FR' // Free Response
          : !Answers.find(s => s.Answer)
            ? 'TL' // Traffic Light
            : '';
      const chart: { color: string, width: string, text: string }[] = [];
      return {
        QnLabel, Answers, type, chart,
        displayInfo: this.displayQns.find(qn => qn === QnLabel) ? true : false,
        displayStudent: this.displayStudents.find(qn => qn === QnLabel) ? true : false
      };
    });
    const max = Math.max(...uniqueQuestions.map(({ Answers }) => Answers.length));
    this.barStats(max);
    uniqueQuestions.forEach(og => {
      const { Answers, type } = og;
      let sum = 0;
      switch (type) {
        case 'MS':
          og.chart = [{
            color: 'grey',
            ...transform(Answers)
          }];
          break;
        case 'FR':
          og.chart = [{
            color: '#007fff',
            ...transform(Answers)
          }];
          break;
        case 'TL':
          const TLorder = ['codeGreen', 'codeOrange', 'codeRed'];
          og.chart = Answers.map(({ Code }) => Code)
            .filter(this.unique)
            .sort((a, b) => TLorder.indexOf(a) - TLorder.indexOf(b))
            .map(Code => {
              switch (Code) {
                case 'codeGreen':
                  return {
                    color: '#77dd77',
                    ...transform(Answers.filter(codeData => codeData.Code === Code))
                  };
                case 'codeOrange':
                  return {
                    color: '#ffb347',
                    ...transform(Answers.filter(codeData => codeData.Code === Code))
                  };
                case 'codeRed':
                  return {
                    color: '#ff6961',
                    ...transform(Answers.filter(codeData => codeData.Code === Code))
                  };
              }
            }).reverse();
          break;
        default:
          const order = ['codeGreen', 'codeRed'];
          og.chart = Answers.map(({ Code }) => Code)
            .filter(this.unique)
            .sort((a, b) => order.indexOf(a) - order.indexOf(b))
            .map(Code => {
              switch (Code) {
                case 'codeGreen':
                  return {
                    color: 'green',
                    ...transform(Answers.filter(codeData => codeData.Code === Code))
                  };
                case 'codeRed':
                  return {
                    color: 'red',
                    ...transform(Answers.filter(codeData => codeData.Code === Code))
                  };
              }
            }).reverse();
      }
      function transform(array: any[]) {
        sum += (array.length / max);
        return {
          width: `${sum * 100}%`,
          text: `${array.length}`
        };
      }
    });
    return uniqueQuestions;
  }

  barStats(max: number) {
    this.max = max;
    function test_prime(n: number) {
      if (n === 1) {
        return true;
      } else if (n === 2) {
        return true;
      } else {
        for (let x = 2; x < n; x++) {
          if (n % x === 0) {
            return false;
          }
        }
        return true;
      }
    }
    let splitTo = 1;
    if (test_prime(max)) {
      splitTo = max;
    } else {
      if (max % 5 === 0) {
        if (max % 10 === 0) {
          splitTo = 10;
        } else {
          splitTo = 5;
        }
      } else if (max % 3 === 0) {
        if (max % 6 === 0) {
          splitTo = 6;
        } else {
          splitTo = 3;
        }
      } else if (max % 2 === 0) {
        if (max % 8 === 0) {
          splitTo = 8;
        } else if (max % 4 === 0) {
          splitTo = 4;
        } else {
          splitTo = 2;
        }
      }
    }
    this.barMax = [];
    for (let index = 0; index <= splitTo; index++) {
      this.barMax.push(Math.round((max * (index / splitTo)) * 100) / 100);
    }
  }

  allDates() {
    this.timeEnd = undefined;
    this.selectedDate = {
      dayText: 'All',
      dateText: '',
      monthText: 'All',
      yearText: 'Dates',
      date: new Date(),
      text: 'all'
    };
    this.getBarData(ref => ref.orderBy('Submitted'));
  }

  transformData(item: firebase.firestore.DocumentData) {
    const data = item as Data;
    data.Submitted = (data.Submitted as firebase.firestore.Timestamp).toDate();
    return data;
  }

  displayMore(QnLabel: string) {
    let index = 0;
    if (this.displayQns.find((qn, i) => {
      if (QnLabel === qn) {
        index = i;
        return true;
      }
    })) {
      this.displayQns.splice(index, 1);
      return false;
    } else {
      this.displayQns.push(QnLabel);
      return true;
    }
  }

  displayStudent(QnLabel: string) {
    let index = 0;
    if (this.displayStudents.find((qn, i) => {
      if (QnLabel === qn) {
        index = i;
        return true;
      }
    })) {
      this.displayStudents.splice(index, 1);
      return false;
    } else {
      this.displayStudents.push(QnLabel);
      return true;
    }
  }

  sliderOnChange(value: number) {
    this.timeEnd = new Date(value);
    this.refreshDate();
  }
}

export interface CourseData {
  ref: AngularFirestoreDocument<firebase.firestore.DocumentData>;
  data: Key;
  key: string;
}

export interface BarInfo {
  QnLabel: string;
  Answers: Answer[];
  type: string;
  chart: ChartData[];
  displayInfo: boolean;
  displayStudent: boolean;
}
export interface Answer extends Data {
  ref: AngularFirestoreDocument<firebase.firestore.DocumentData>;
}

export interface ChartData {
  color: string;
  width: string;
  text: string;
}

export interface DateInfo {
  dayText: string;
  dateText: string;
  monthText: string;
  yearText: string;
  date: Date;
  text: string;
}
