import { Injectable, isDevMode } from '@angular/core';
import * as firebase from 'firebase/app';
import { Router } from '@angular/router';
import { AngularFireAuth } from '@angular/fire/auth';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  authState: firebase.User | any = null;
  redirectUrl: string;

  constructor(private af: AngularFireAuth, private router: Router) {
    af.authState.subscribe(a => {
      return this.authState = a;
    });
  }

  setRedirectUrl(url: string) {
    this.redirectUrl = url;
  }

  get authenticated(): boolean {
    return this.authState !== null;
  }

  get $authenticated(): Observable<boolean> {
    return this.af.authState.pipe(map(user => user !== null));
  }

  get currentUser(): any {
    return this.authenticated ? this.authState : null;
  }

  get currentUserId(): string {
    return this.authenticated ? this.authState.uid : '';
  }

  reRoute() {
    if (!this.authenticated) {
      this.router.navigate(['/dashboard']);
    }
  }

  logout() {
    this.af.auth.signOut();
    this.router.navigate(['/login']);
  }

  emailSignUp(email: string, password: string, name: string): Promise<any> {
    return this.af.auth.createUserWithEmailAndPassword(email, password)
      .then(user => user.user.updateProfile({ displayName: name }));
  }

  emailLogin(email: string, password: string): Promise<any> {
    return this.af.auth.signInWithEmailAndPassword(email, password);
  }
  login(email: string, password: string): void {
    this.af.auth.signInWithEmailAndPassword(email, password).catch(err => console.log(err));
  }
}
