import { Component, EventEmitter, Output, Input, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Key, Interval } from '../../../core/data';
import { KeyService } from '../../../core/key.service';
import { AngularFirestoreDocument } from '@angular/fire/firestore';

export interface CourseData {
  data: Key;
  key: string;
  ref: AngularFirestoreDocument<unknown>;
}

@Component({
  selector: 'app-course',
  templateUrl: './course.component.html',
  styleUrls: ['./course.component.scss']
})
export class CourseComponent implements OnInit {
  @Input() course: CourseData;
  // tslint:disable-next-line: no-output-native
  @Output() close = new EventEmitter();

  types: { value: number, text: string }[];
  courseForm: FormGroup;
  sameDate: boolean;
  entireDay: boolean;
  statusColor: string;
  loading: boolean;
  done: boolean;

  constructor(private ks: KeyService) {
    this.courseForm = new FormGroup({
      name: new FormControl(),
      type: new FormControl()
    });
    this.courseForm.valueChanges.subscribe(() => this.done = false);
    this.types = [{ value: 0, text: 'Not Published' }, { value: 1, text: 'Published' }, { value: 2, text: 'Interval' }];
    this.courseForm.controls.type.setValue(this.types[0].value);
    this.done = false;
  }

  ngOnInit() {
    const course = this.course;
    if (course) {
      const { data } = course;
      this.courseForm.get('name').setValue(data.name);
      this.courseForm.get('type').setValue(data.type);
      if (data.type === 2) {
        const { interval } = data;
        const { sameDate, entireDay } = interval;
        let { start, end } = interval;
        start = firebaseDate(start);
        end = firebaseDate(end);
        this.sameDate = sameDate;
        this.entireDay = entireDay;
        const value = {} as FormValue;
        value.type = data.type;
        value.startDate = start;
        if (!this.entireDay || !this.sameDate) {
          if (this.entireDay) {
            value.endDate = end;
          } else {
            if (!this.sameDate) {
              value.endDate = end;
            }
            value.startTime = dateToTime(start);
            value.endTime = dateToTime(end);
          }
        }
        this.changeForm(value);
      } else {
        this.sameDate = true;
        this.entireDay = false;
      }
    }
    this.changeCircle();
    function dateToTime(date: Date): string {
      const hours = date.getHours() === 0 ? 12 : date.getHours();
      const minutes = date.getMinutes().toString().padStart(2, '0');
      return `${hours}:${minutes} ${date.getHours() >= 12 ? 'PM' : 'AM'}`;
    }
    function firebaseDate(date: Date | firebase.firestore.Timestamp): Date {
      return (date as firebase.firestore.Timestamp).toDate();
    }
  }

  cancel() {
    this.close.emit(null);
  }

  onSubmit(form: FormGroup) {
    if (form.valid) {
      this.loading = true;
      const value = form.value as FormValue;
      const config = {
        name: value.name,
        type: value.type
      } as Key;
      if (config.type === 2) {
        const interval = config.interval = { sameDate: this.sameDate, entireDay: this.entireDay } as Interval;
        if (this.sameDate && this.entireDay) { // Single Date
          const date = value.startDate;
          interval.start = new Date(date);
          date.setHours(23, 59, 59, 999);
          interval.end = date;
        } else if (this.sameDate) { // Single Date, with Start and End Time
          interval.start = updateTime(value.startDate, value.startTime);
          interval.end = updateTime(value.startDate, value.endTime);
        } else if (this.entireDay) { // Start and End Date, no Time
          interval.start = value.startDate;
          value.endDate.setDate(value.endDate.getDate() + 1);
          interval.end = value.endDate;
        } else { // All
          interval.start = updateTime(value.startDate, value.startTime);
          interval.end = updateTime(value.endDate, value.endTime);
        }
      }
      (async (a: Key) => {
        const data = { name_lowercase: a.name.toLowerCase(), ...a };
        if (this.course) {
          await this.course.ref.update(data);
        } else {
          const createdCourse = await this.ks.generateCourse(data);
          this.course = {
            data: (await createdCourse.get()).data() as Key,
            key: createdCourse.id,
            ref: this.ks.referenceToAngularReference(createdCourse)
          };
          this.ngOnInit();
        }
      })(config).catch(err => alert('Error adding/updating Course' + err))
        .finally(() => { this.loading = false; this.done = true; });
      function updateTime(date: Date, time: string) {
        const holder = /(.+):(.+) (.+)/.exec(time);
        if (Number(holder[1]) === 12) {
          if (holder[3] === 'AM') {
            date.setHours(0);
          } else {
            date.setHours(12);
          }
        } else {
          date.setHours(Number(holder[1]) + (holder[3] === 'AM' ? 0 : 12));
        }
        date.setMinutes(+holder[2]);
        return new Date(date);
      }
    }
  }

  changeCircle() {
    const type = this.courseForm.value.type;
    switch (type) {
      case 0:
        this.statusColor = 'not-published';
        break;
      case 1:
        this.statusColor = 'published';
        break;
      case 2:
        this.statusColor = 'interval';
        break;
    }
  }

  changeForm(val?: FormValue) {
    const form = this.courseForm;
    const value = val ? val : form.value;
    ['startDate', 'endDate', 'startTime', 'endTime']
      .forEach((control: string) => form.removeControl(control));
    if (value.type === 2) {
      date('start');
      if (!this.entireDay || !this.sameDate) {
        if (this.entireDay) {
          date('end');
        } else {
          if (!this.sameDate) {
            date('end');
          }
          time('start');
          time('end');
        }
      }
      function date(name: string) {
        form.addControl(`${name}Date`, new FormControl(value[`${name}Date`], Validators.required));
      }
      function time(name: string) {
        form.addControl(`${name}Time`, new FormControl(value[`${name}Time`], Validators.required));
      }
    }
  }

  delete(course: CourseData) {
    if (confirm(`Are you sure you want to delete this course?`)) {
      course.ref.delete();
      this.cancel();
    }
  }

  downloadKey(course: CourseData) {
    this.ks.generateAPIKeyFile(course.ref);
  }
}

export interface FormValue {
  name: string;
  type: number;
  startTime?: string;
  endTime?: string;
  startDate?: Date;
  endDate?: Date;
}
